import uniIcons from "@/components/uni-icons/uni-icons.vue"
export default {
	data() {
		return {
			id:'',
			myData:{},
			sessionId:''
		}
	},
	onLoad(option) {
		this.id = option.id
		this.sessionId = option.sessionId
		this.init()
	},
	methods: {
		init(){
			uni.request({
				url:'https://www.nssnail.com/wx/getNoticeById',
				data:{
					id:this.id,
					sessionId:this.sessionId
				},
				success: (res) => {
					if(res.data.stat == 1){
						this.myData = res.data.data
					}else if(res.data.stat == 3){
						uni.showModal({
						    title: '提示',
						    content: '登录过期，请重新登录',
						    success: function (res) {
						        if (res.confirm) {
						            uni.navigateTo({
						            	url:'../myInit/myInit'
						            })
						        }
						    }
						});
					}
				}
			})
		}
	},
	components:{
		uniIcons
	}
}
